body,
html {
  font-family: 'Poppins', sans-serif;
  padding: 0;
  margin: 0;
  
}

* {
  box-sizing: border-box;
}
.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  background-color: rgb(32, 32, 36);
  padding-top: 3em;
  height: 100%;
  min-height: 100vh;
}

.wrapper h1 {
  color: #f0f0f0;
  text-align: center;
}

.wrapper p {
  color: #cacaca;
  text-align: center;
}

.container {
  width: 100%;
  max-width: 1100px;
  margin-top: 5em;
  padding: 1em;
}

.field-group {
  display: flex;
  width: 100%;
}

.input {
  display: block;
  width:100%;
  margin-right: 1em;
  background-color:rgba(108, 122, 137, 1);
  border: none;
  font-size: 1.5rem;
  padding: 1em;
  border-radius: 5px;
  color: white;
}
.input::placeholder {
  color: rgb(167, 185, 204);
  text-transform: uppercase;
  font-style: italic;
  font-weight: bold;
  font-size: 1.5rem;
}

.button {
  white-space: nowrap;
  padding: 0 2em;
  font-size: 1.4rem;
  color: #232323;
  background-color: rgb(123, 239, 178);
  border-radius: 5px;
  border: none;
  text-transform: uppercase;
  font-weight: bold;
}

.highlight {
  color: white;
  padding: 0 3px;
  border-radius: 5px;
  background-color: rgba(211, 84, 0, 1);
}

#resultsList {
  list-style: none;
  display: flex;
  align-self: flex-start;
  flex-wrap: wrap;
  padding: 0;
}
#resultsList li {
  width: 120px;
  height: 120px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #e4e9ed;
  padding: 1em;
  border-radius: 5px;
  margin-bottom: 1em;
}

#resultsList li:not(:last-child) {
  margin-right: 1em;
}

#resultsList li i {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

#resultsList li p {
  color: #232323;
  margin: 0;
  text-align: center;
  word-break: break-word;
}


@media screen and (max-width: 768px) {
  .field-group {
    flex-direction: column;
  }

  .button {
    height: 70px;
    width: 200px;
    margin: 0 auto;
    margin-top: 2em;
  }
}

@media screen and (max-width: 425px) {
  #resultsList {
    justify-content: center;
  }
}